.ettention {
    font-size: 30px;
    font-weight: bold;
    color: red;
    margin: 50px 0 0 50px;
    animation: name 1s infinite alternate;
}

@keyframes name {
    from {
        color: yellowgreen;
    }
    to {
        color: green;
    }
}

.wrap input {
    display: flex;
    margin-bottom: 6px;
    border-radius: 6px;
    outline: none;
    border: 1px solid yellowgreen;
    padding: 5px 0 5px 3px;
    background-color: inherit;
}
.form {
    margin: 15px 0 0 30px;
}

.btn {
    background-color: inherit;
    border-radius: 6px;
    color: #000;
    border: 0;
    padding: 7px;
    cursor: pointer;
    margin-top: 5px;
    border: 1px solid yellowgreen;
}
.btn:hover {
    background-color: yellowgreen;
    color: #fff;
}
.loginBtn {
    color: red;
    font-weight: bold;
}