.header {
	height: 70px;
	background: #00B74A	;
	padding: 0 40px;
	display: flex;
	position: relative;
	z-index: 10;
	justify-content: space-between;
	box-shadow: -2px 0px 2px 2px #07e40ede;
}
.header__title, .header__login {
	font-family: 'Nunito';
    font-weight: 700;
    font-size: 25px;
    color: #fff;
    line-height: 70px;
}
.logOut {
	border-top: 2px solid #26d32396;
	border-bottom: 2px solid #26d32396;
	border-radius: 5px;
}
.header__login button {
	border: 1px solid green;
	border-radius: 4px;
}

.login {
	color: #fff;
	text-decoration: none;
	border-top: 2px solid #26d32396;
	border-bottom: 2px solid #26d32396;
	border-radius: 5px;
}