.users {
    margin: 20px 0 0 20px;
    font-family: 'Nunito';
}

h2 {
    font-size: 25px;
    font-weight: bold;
    margin-bottom: 30px;
}

.user_item {
    display: flex;
    margin-bottom: 20px;
    margin-top: 20px;
}
.user_item__img img {
    width: 50px;
    height: 50px;
    border-radius: 50%;
    margin-bottom: 15px;
    margin-left: 20px;
}
.user_item__subscribe {
    border: 1px solid yellowgreen;
    border-radius: 5px;
    background: transparent;
    outline: none;
    cursor: pointer;
}
.user_item__subscribe:hover {
    background: yellowgreen;
    color: #fff;
}

.user_item__right {
    padding: 10px;
    margin-left: 20px;
    border: 1px solid yellowgreen;
    border-radius: 4px;
}
.user_item__name {
    font-size: 25px;
    font-weight: bold;
    margin-bottom: 30px;
}
.user_item__information {
    font-size: 18px;
}
span {
    margin-right: 10px;
    margin-bottom: 20px;
    cursor: pointer;
}
.activeSlide {
    font-weight: bold;
}

.countUser {
    width: 800px;
    height: 40px;
    overflow-x: scroll;
}